import React from "react";

class AboutRouter extends React.Component{
    constructor(props){
        super(props)
    }

    componentWillMount(){

    }

    render(){
        return (
            <div >
                <pre>{' <Route exact path="/marketDetail/:id" component={MarketDetail}/>'}</pre>
                <pre>{'this.props.history.push(`marketDetail/${1}`)'}</pre>
                <pre>{'this.detailObj = JSON.parse(this.props.match.params.id);'}</pre>
                <pre>{'this.props.history.goBack();'}</pre>


            </div>
        )
    }
}
export default AboutRouter;
